	module.exports = {"data":{"post":{"html":"<p>That's enough of the <a href=\"http://www.papascott.de/bl.php\">Bloglines layout</a> for now. It might come back some day, though. I find the layout very functional, and I may work out actual templates with a sidebar and more weblog-like features.</p>\n<p>The trick yesterday was to use wget to get the public copy of my blog from bloglines, insert a &lt;base href...&gt; in the html header so the relative links worked correctly, and add bl.php to the beginning of the DirectoryIndex line in my .htaccess to make it the default page. </p>\n<p>This was all powered by an hourly cron job, since bloglines reads my blog only once per hour. It's not a script, but a one-liner with sed. If I put the long bloglines URL into a tinyurl, it looks like:</p>\n<p><code>/usr/local/bin/wget -O - 'http://tinyurl.com/5zekr' | sed -e 's/&lt;title>/&lt;base href=\"http:&#92;/&#92;/www.bloglines.com&#92;/\">&lt;title>PapaScott/' > /usr/local/www/data/bl.php</code></p>","fields":{"title":"Enough For Now","date":"11 Sep 2004","url":"https://papascott.de/2004-09-11-enough-for-now/"}}},"pathContext":{"slug":"/2004-09-11-enough-for-now/","prev":{"fields":{"title":"There He Goes Again","date":"10 Sep 2004","slug":"/2004-09-10-there-he-goes-again/"}},"next":{"fields":{"title":"Remember the Day","date":"11 Sep 2004","slug":"/2004-09-11-remember-the-day/"}}}}

